<template>
    <div class="chart" ref="chartWrapper"></div>
</template>

<script>
    import {debounce} from '@/utils'

    export default {
        name: 'echarts',
        props: {
            mapType: {
                type: String,
                default: ''
            },
            chartData: {
                type: Object,  //echartssetOptions
                default: function () {
                    return {
                        loading: true,
                        options: {},
                        __resizeHandler:null
                    }
                }
            }
        },
        data() {
            return {
                chart: null,
                loadingInstance: null,
                options: {
                    color: [
                        '#FF8E35',
                        '#99cc27',
                        '#42bada',
                        '#FFEB3B',
                        '#6b8dee',
                        '#30a86d',
                    ],
                    title: {
                        textStyle: {
                            color: "#333",
                            fontSize: 14,
                            fontWeight: 420
                        },
                        x: 'center'
                    },
                    //
                    grid: {
                        left: '0',
                        right: '0',
                        bottom: '0',
                        top: 35,
                        // grid 
                        containLabel: true
                    },
                },
                baseOptions: null,
                firstLoad: true
            }
        },
        activated() {
            //  echart
            this.initChart();

            if (this.firstLoad) {
                // loading
                this.showLoading();
                this.firstLoad = false;
            } else {
                // loading
                this.resetChart();
            }

            this.__resizeHandler = debounce(() => {
                if (!!this.chart) {
                    this.chart.resize()
                }
            }, 100);

            // 
            window.addEventListener('resize', this.__resizeHandler);
            this.sidebarElm = document.getElementsByClassName('aside')[0];
            this.sidebarElm && this.sidebarElm.addEventListener('transitionend', this.sidebarResizeHandler)
        },
        deactivated() {
            this.destroyChart();
        },
        beforeDestroy() {
            this.destroyChart();
        },
        watch: {
            'chartData.loading': {
                deep: true,
                handler(load) {
                    //loadingecharts
                    if (load) {
                        this.showLoading()
                    } else {
                        this.resetChart();
                    }
                }
            }
        },
        methods: {
            sidebarResizeHandler(event) {
                if (event.propertyName === 'width') {
                    this.__resizeHandler()
                }
            },
            destroyChart() {
                //  echart 
                if (!!this.chart) {
                    this.chart.dispose();
                    this.chart = null
                } else {
                    return false;
                }
                // 
                window.removeEventListener('resize', this.__resizeHandler);
                this.sidebarElm && this.sidebarElm.removeEventListener('transitionend', this.sidebarResizeHandler);
            },
            resetChart() {
                if (!!this.chart) {
                    // 
                    this.chart.clear();
                    // 
                    this.chart.setOption(this.baseOptions);
                    // 
                    this.chart.setOption(this.chartData.options);
                }
                // loadingfalse 
                this.hideLoading();
            },
            showLoading() {
                // this.loadingInstance = this.$loading({
                //     target: this.$refs['chartWrapper'],
                //     background: 'rgba(0, 0, 0, 0.6)',
                //     spinner: 'el-icon-loading',
                //     text: ''
                // });
            },
            hideLoading() {
                // if (!!this.loadingInstance) {
                //     this.loadingInstance.close();
                //     this.loadingInstance = null;
                // }
            },
            initChart() {
                // 
                this.chart = this.$eCharts.init(this.$el);
                // 
                this.chart.setOption(this.options);
                this.baseOptions = this.chart.getOption();
            }
        }
    }
</script>

<style lang="scss" type="text/scss">
    .chart {
        width: 100%;
        height: 100%;

        div:nth-of-type(2) {
            z-index: 2;
        }
    }
</style>
