<template>
    <v-echarts :chartData="chartData"></v-echarts>
</template>

<script type="text/ecmascript-6">
    import vEcharts from './echarts'
    import _merge from 'lodash.merge'

    export default {
        name: 'chartLine',
        props: ['data'],
        data() {
            return {
                baseOptions: null,
                chartData: {
                    loading: true,
                    options: {
                        tooltip: {
                            trigger: 'axis'
                        },
                        grid: {
                            top:'20px'
                        },
                        legend: {
                            icon: 'rect',
                            itemWidth: 14,
                            itemHeight: 5,
                            itemGap: 13,
                            data: [],
                            right: '10',
                            textStyle: {
                                fontSize: 12,
                                color: '#333'
                            }
                        },
                        dataZoom: [
                            {
                                type: 'inside'
                            }],
                        yAxis: {
                            axisLine: {
                                lineStyle: {
                                    color: '#3c3c3c'
                                }
                            },
                            splitLine: {
                                show:true,
                                lineStyle: {
                                    type: 'dashed',
                                    color: '#e4e4e4'
                                }
                            }
                        },
                        xAxis: {
                            axisLine: {
                                lineStyle: {
                                    color: '#3c3c3c'
                                }
                            },
                            data: []
                        },
                        series: []
                    }
                },
                baseSeries:{
                    type: 'line',
                    symbol: 'circle',
                    opacity: 0.4,
                    areaStyle: {
                        normal: {
                            color: new this.$eCharts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: 'rgba(255, 232, 0, 0.3)'
                            }, {
                                offset: 0.8,
                                color: 'rgba(255, 232, 0, 0)'
                            }], false),
                            shadowBlur: 12,
                            shadowColor: 'rgba(0, 0, 0, 0.1)'
                        }
                    },
                    lineStyle: {
                        normal: {
                            width: 1
                        }
                    },
                    itemStyle: {
                        borderColor: 'rgba(137,189,2,0.2)',
                        borderWidth: 1
                    }
                }
            }
        },
        created() {
            // vue
            this.baseOptions = Object.assign({},this.chartData);
        },
        watch: {
            'data.loading': {
                deep: true,
                handler(n, o) {
                    //  loading false
                    if (!n) {
                        //  options 
                        let baseOptions = Object.assign({},this.baseOptions);

                        this.data.options.series = this.data.options.series.map(item => {
                            return {
                                ...item,
                                ...this.baseSeries
                            }
                        });

                        // 
                        this.chartData = _merge(baseOptions, this.data);
                    }else{
                        this.chartData.loading = n;
                    }
                }
            }
        },
        components: {
            vEcharts
        }
    }

</script>
