<template>
    <v-echarts :chartData="chartData"></v-echarts>
</template>

<script>
    import vEcharts from './echarts'
    import _merge from 'lodash.merge'

    export default {
        name: 'chartBar',
        props: ['data'],
        data() {
            return {
                baseOptions: null,
                chartData:{
                    loading: true,
                    options: {
                        tooltip : {
                            trigger: 'axis',
                            axisPointer: { // 
                                type: 'shadow' // 'line' | 'shadow'
                            }
                        },
                        grid: {
                            top:'30px'
                        },
                        yAxis: [{
                            splitLine: {
                                // grid 
                                lineStyle: {
                                    type: 'dashed',
                                    color: '#e4e4e4'
                                }
                            },
                            axisLine: {
                                lineStyle: {
                                    color: '#333' //
                                }
                            },
                            axisLabel: {
                                color: '#333',  //
                                fontSize: 12
                            }
                        }],
                        xAxis: [{
                            splitLine: {
                                // grid 
                                lineStyle: {
                                    color: ['#333']
                                }
                            },
                            axisLine: {
                                lineStyle: {
                                    color: '#333' //
                                }
                            },
                            axisLabel: {
                                color: '#333', //
                                fontSize: 12
                            }
                        }],
                        series: [
                            {
                                type: 'bar',
                                data: []
                            }
                        ]
                    }
                }
            }
        },
        created(){
            // vue
            this.baseOptions = Object.assign({},this.chartData)
        },
        watch: {
            'data.loading':{
                deep: true,
                handler(n, o) {
                    //  loading false
                    if(!n){
                        //  options 
                        let baseOptions = Object.assign({},this.baseOptions);
                        // 
                        this.chartData = _merge(baseOptions, this.data);
                    }else{
                        this.chartData.loading = n;
                    }
                }
            }
        },
        components: {
            vEcharts
        }
    }
</script>
