<template>
    <v-echarts :chartData="chartData"></v-echarts>
</template>

<script type="text/ecmascript-6">
    import vEcharts from './echarts'
    import _merge from 'lodash.merge'

    export default {
        name: 'chartCalendar',
        props: ['data', 'handler'],
        data() {
            return {
                baseOptions: null,
                chartData: {
                    loading: true,
                    options: {
                        tooltip: {
                            position: 'top',
                            formatter:(params, ticket, callback) => {
                                return params.marker + params.value[0] + ": " + params.value[1];
                            }
                        },
                        visualMap: {
                            min: 0,
                            max: 200,
                            type: 'piecewise',
                            orient: 'horizontal',
                            itemWidth: 14,
                            itemHeight: 5,
                            itemGap: 13,
                            color:[
                                '#00c3cc',
                                '#17cc27',
                                '#16cc7b',
                                '#8ecc38',
                                '#b3cc42',
                            ],
                            top: 10,
                            right:10,
                            textStyle: {
                                color: '#333'
                            }
                        },

                        calendar: {
                            top: 50,
                            left: 50,
                            right: 0,
                            bottom: 0,
                            cellSize: ['auto', 13],
                            monthLabel:{
                                formatter: "{M}",
                                color: '#333'
                            },
                            dayLabel:{
                                nameMap: "cn",
                                color: '#333'
                            },
                            itemStyle: {
                                normal: {borderWidth: 0.5}
                            }
                        },
                        series: [{
                            type: 'heatmap',
                            coordinateSystem: 'calendar',
                            data: []
                        }]
                    }
                }
            }
        },
        created() {
            // vue
            this.baseOptions =  Object.assign({},this.chartData);
        },
        watch: {
            'data.loading': {
                deep: true,
                handler(n, o) {
                    //  loading false
                    if (!n) {
                        //  options 
                        let baseOptions = Object.assign({},this.baseOptions);
                        let max = 0;
                        this.data.options.series[0].data = this.data.options.series[0].data.filter(item=> item[1] > 0);
                        this.data.options.series[0].data.forEach(item => max = max > item[1] ? max : item[1]);
                        baseOptions.options.visualMap.max = Math.ceil(max/10) * 10;
                        // 
                        this.chartData = _merge(baseOptions, this.data);
                    } else {
                        this.chartData.loading = n;
                    }
                }
            }
        },
        components: {
            vEcharts
        }
    }

</script>
