<template>
    <div class="stream-chart">
        <div class="chart-box">
            <h3></h3>
            <div class="line-chart-box">
                <v-chart-line :data="chartLineData"></v-chart-line>
            </div>
        </div>
        <div class="chart-box">
            <h3></h3>
            <div class="calendar-chart-box">
                <v-chart-calendar :data="chartCalendarData"></v-chart-calendar>
            </div>
        </div>
        <div class="chart-box">
            <h3></h3>
            <div class="bar-chart-box">
                <v-chart-bar :data="dataBar"></v-chart-bar>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import vChartLine from '@/components/Echarts/chartLine'
    import vChartBar from '@/components/Echarts/chartBar'
    import vChartCalendar from '@/components/Echarts/chartCalendar'
    import {testData} from "./testData";
    import {handleError, logic} from "@/utils";
    import {apiStreamByYear, apiStreamIO} from "@/api";

    export default {
        name: 'streamManage',
        data() {
            return {
                chartLineData: {
                    loading: true,
                    options: {
                        xAxis: {
                            data: []
                        },
                        legend: {
                            data: []
                        },
                        series: []
                    }
                },
                chartCalendarData: {
                    loading: true,
                    options: {
                        range: new Date().getFullYear(),
                        series: [{
                            data: []
                        }]
                    }
                },
                dataBar: {
                    loading: true,
                    options: {
                        title: {
                            textStyle: {
                                textAlign: 'center'
                            }
                        },
                        legend: {
                            data: ['', '', ''],
                            top: 10,
                            right:10,
                            itemWidth: 14,
                            itemHeight: 5,
                            itemGap: 13,
                        },
                        series: [],
                        yAxis: [{
                            type: 'value',
                            axisLabel: {
                                formatter: '{value}'
                            }
                        }],
                        xAxis: [{
                            type: 'category',
                            data: []  //
                        }]
                    }
                },
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            /**
             * 
             */
            async init() {
                await this.getLineData();
                await this.getCalendarData();
                await this.getBarData();
            },
            /**
             * line chart
             */
            getLineData() {
                setTimeout(() => {
                    let result = testData;

                    let xAxisData = [],
                        legend = [],
                        series = [];
                    for (let key in result) {
                        let lineData = [];
                        result[key].data.forEach((item) => {
                            if (key === 'booth') {
                                xAxisData.push(item.time);
                            }
                            lineData.push(Math.round(Math.random() * 10 + Math.random() * 10));
                        });
                        legend.push(result[key].name);
                        series.push({
                            name: result[key].name,
                            data: lineData
                        })
                    }

                    this.chartLineData.options.xAxis.data = xAxisData;
                    this.chartLineData.options.series = series;
                    this.chartLineData.options.legend.data = legend;
                    this.chartLineData.loading = false;

                }, 100);

            },
            /**
             * bar chart
             */
            getBarData() {

                this.dataBar.loading = true;

                apiStreamIO()
                    .then(res => {
                        /**
                         * 
                         */
                        logic(res, result => {
                                this.dataBar.options.series = [
                                    {
                                        type: 'bar',
                                        name:'',
                                        stack:'',
                                        data: result.data.map(item => {
                                            return item['income'];
                                        })
                                    },
                                    {
                                        type: 'bar',
                                        name:'',
                                        stack:'',
                                        data: result.data.map(item => {
                                            return -item['expend'];
                                        })
                                    },
                                    {
                                        type: 'bar',
                                        name:'',
                                        data: result.data.map(item => {
                                            return item['income'] - item['expend'];
                                        })
                                    }
                                ];
                                //
                                this.dataBar.options.xAxis[0].data = result.data.map(item => {
                                    return item['date'];
                                });
                            },
                            msg => {
                                this.$message.error(msg)
                            });
                    })
                    .catch(err => {
                        handleError(err);
                    })
                    .finally(() => {
                        this.dataBar.loading = false;
                    });
            },
            /**
             * calendar chart
             */
            getCalendarData() {
                this.chartCalendarData.loading = true;
                apiStreamByYear()
                    .then(res => {
                        /**
                         * 
                         */
                        logic(res, result => {
                                this.chartCalendarData.options.series[0].data = result.data;
                                this.chartCalendarData.loading = false;
                            },
                            msg => {
                                this.$message.error(msg)
                            });
                    })
                    .catch(err => {
                        handleError(err);
                    })
                    .finally(() => {
                        this.chartCalendarData.loading = false;
                    });
            },
        },
        components: {
            vChartLine,
            vChartBar,
            vChartCalendar
        }
    }
</script>

<style lang="scss" type="text/scss">
    @import "~@/scss/_setting.scss";
    @import "~@/scss/_mixin.scss";

    .stream-chart {
        min-height: 100%;
        box-sizing: border-box;
        h3 {
            font-size: 14px;
            border-left: 3px solid #75e2ff;
            padding-left: 10px;
            line-height: 26px;
            font-weight: 500;
        }

        .chart-box {
            background-color: $white;
            padding: 10px;
            box-shadow: 0 0 5px #c7c7c7;
            margin-bottom: 10px;
            overflow: hidden;
        }

        .line-chart-box {
            height: 250px;
        }

        .calendar-chart-box {
            height: 200px;
        }

        .bar-chart-box {
            height: 400px;
        }
    }
</style>
